import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import clsx from "clsx";
import Card from "@material-ui/core/Card";
import CardHeader from "@material-ui/core/CardHeader";
import CardContent from "@material-ui/core/CardContent";
import CardActions from "@material-ui/core/CardActions";
import Collapse from "@material-ui/core/Collapse";
import Avatar from "@material-ui/core/Avatar";
import IconButton from "@material-ui/core/IconButton";
import Typography from "@material-ui/core/Typography";
import { red } from "@material-ui/core/colors";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import Tooltip from "@material-ui/core/Tooltip";
import Breadcrumbs from "@material-ui/core/Breadcrumbs";
import NavigateNextIcon from "@material-ui/icons/NavigateNext";
import Divider from "@material-ui/core/Divider";

const useStyles = makeStyles((theme) => ({
  root: {},
  media: {
    height: 0,
    paddingTop: "56.25%", // 16:9
  },
  expand: {
    transform: "rotate(0deg)",
    marginLeft: "auto",
    transition: theme.transitions.create("transform", {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: "rotate(180deg)",
  },
  avatar: {
    backgroundColor: red[500],
    cursor: "pointer",
  },
}));

export default function PurchasesList() {
  const classes = useStyles();
  const [expanded, setExpanded] = React.useState(false);
  const [sortBy, setSortBy] = React.useState(null);

  const handleExpandClick = () => {
    setExpanded(!expanded);
  };

  const sortStudent = (student) => {
    setSortBy(student);
  };
  const mixUp = () => {
    setSortBy(null);
  };
  return (
    <div className="container" style={{ marginTop: "6em" }}>
      <Breadcrumbs
        separator={<NavigateNextIcon fontSize="small" />}
        aria-label="breadcrumb"
      >
        <Typography
          color="inherit"
          style={{ cursor: "pointer" }}
          onClick={() => mixUp()}
        >
          Purchase List
        </Typography>
        {sortBy ? (
          <Typography color="textPrimary"> {sortBy} </Typography>
        ) : null}
      </Breadcrumbs>
      <Divider style={{ marginTop: ".4em" }} />
      {[1, 2, 3, 4, 5].map((element) => {
        return (
          <Card className={(classes.root, "mt-4")} elevation={4} key={element}>
            <CardHeader
              avatar={
                <Avatar
                  aria-label="recipe"
                  className={classes.avatar}
                  onClick={() => sortStudent(element)}
                >
                  R
                </Avatar>
              }
              title="Student ka naam"
              subheader="Taarikh kam karida"
            />
            <CardContent>
              <Typography paragraph>
                <strong>Course : </strong> Course ka naam
              </Typography>
              <Typography paragraph>
                <strong>Amount Paid : </strong> &#8377; 1000
              </Typography>
              <Typography paragraph>
                <strong>Enrolled Date : </strong> Course Enrolled Date
              </Typography>
              <Typography paragraph>
                <strong>Status : </strong> In Progress/Completed
              </Typography>
            </CardContent>
            <CardActions disableSpacing>
              <Tooltip title="More Details">
                <IconButton
                  className={clsx(classes.expand, {
                    [classes.expandOpen]: expanded,
                  })}
                  onClick={handleExpandClick}
                  aria-expanded={expanded}
                  aria-label="show more"
                >
                  <ExpandMoreIcon />
                </IconButton>
              </Tooltip>
            </CardActions>
            <Collapse in={expanded} timeout="auto" unmountOnExit>
              <CardContent>
                <Typography paragraph>
                  {" "}
                  <strong>School : </strong> School ka naam{" "}
                </Typography>
                <Typography paragraph>
                  {" "}
                  <strong>Class : </strong> Class{" "}
                </Typography>
                <Typography paragraph>
                  {" "}
                  <strong>Total Courses Baught : </strong> 10{" "}
                </Typography>
              </CardContent>
            </Collapse>
          </Card>
        );
      })}
    </div>
  );
}
